import React from 'react'
import { FaHeart } from "react-icons/fa";

export default function ProductCart({ data }) {
    return (
        <div className='shadow-lg'>
            <img src={data.thumbnail} alt="" />
            <h4 className='py-3 text-center'>Side and End Tables</h4>
            <h3 className='font-bold text-center py-2'>Hrithvik Stool</h3>
            <hr className='my-4 border-0 h-0.5 bg-[#eee]'/>
            <div className='py-3 text-center'>
                <span className='line-through'>Rs. 7,000</span><span> Rs. 6,000</span>
            </div>
            <div className='py-3'>
                <center>
                    <button className=''><FaHeart /></button>
                    <button className=''>Add To Card</button>
                </center>
            </div>
        </div>
    )
}
